.preloader {
    position: fixed;
    top: 0;
    left: 0;
    margin: auto;
    width: 100vw;
    height: 100vh;
    opacity: 1;
    transition: all 0.3s ease;
    transition-duration: 220ms;
    transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
    z-index: 99;
    transition-delay: 0.3s;
    opacity: 0;
    pointer-events: none;

    * {
        transition: all 0.3s ease;
        transition-duration: 500ms;
        transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
    }

	&.show {
		opacity: 1;
		pointer-events: all;
	}
    .transition-overlay {
        width: 100vw;
        height: 100vh;
        position: fixed;
        right: calc(-100% - 200px);
        bottom: 0;
        background: #797979;
        z-index: 9999;
        -webkit-transition: all 0.3s ease;
        -moz-transition: all 0.3s ease;
        transition: all 0.3s ease;
        transition-duration: 500ms;
        -webkit-transition-duration: 500ms;
        transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
        -webkit-transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
        visibility: hidden;
    }

    .inner {
        width: 100vw;
        height: 100vh;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        flex-direction: column;
        padding: 5% 0;
        box-sizing: border-box;
    }

    .loading-text {
        .percentage {
            display: inline-block;
            width: 95px;
        }
    }

    .bottom {
        position: absolute;
        bottom: 3%;
        left: 50%;
        transform: translateX(-50%);
    }

    $img-w: 1920px;
    $img-h: 65px;
    $shrink: 2;
    $ratio: 1 / $shrink;
    $speed: 20s;

    .slider {
        width: 100%;
        top: calc(50% - #{$img-h * $ratio / 2});
        overflow: hidden;
        margin-bottom: 30px;
        .slider-row {
            width: $img-w;
            height: $img-h;
            background-image: url(#{$stripImgScr});
            background-size: contain;
            animation: slide $speed linear infinite;
        }
    }

    @keyframes slide {
        from {
            background-position-x: 0;
        }
        to {
            background-position-x: -$img-w;
        }
    }

    .page-loaded & {
        opacity: 0;
        pointer-events: none;
    }
}
